.container_plan {
    position:relative;
    place-items: center;
    justify-content: flex-end;
    display: flex;
    padding: 50px;
    overflow-y: auto;
}

.container_plan img {
    display:block;
    justify-content: center;
    width: 50%;
    height: auto;
    border-radius: 15%;
}
